<template lang="html">
  <div class="Company-details">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
        <Col span="4" class="font-16">
          <h3>{{$t('common.Cooperative_details')}}</h3>
        </Col>
        
        <Col span="7">

        </Col>
        <Col span="6">
          <Button type="info" class="mgl-10" @click="succ" v-show="change_btn =='Renewal'">{{$t('common.Renewal')}}</Button>
          <Button type="info" class="mgl-20" @click="cancel" v-show="change_btn =='Pause'">{{$t('common.Pause')}}</Button>
          <Button type="warning" class="mgl-20" @click="cancel" >{{$t('common.Return')}}</Button>
        </Col>
    </Row>
    <Row class="Company-details-title">
      <Col span="20">
        <h4>{{$t('common.Enterprise_basic_information')}}</h4>
      </Col>
      <Col span="4">
        <Button type="info" class="mgl-10" >{{$t('common.Edit')}}</Button>
      </Col>
    </Row>
    <div >
      
    </div>
    <Table border :columns="head"  :data="companyInfo" :show-header="false"></Table>

    <div class="Company-details-title">
      <h4>企业非财务材料</h4>
    </div>
    <Table border :columns="nonfinancial_materials" :data="nonfinancial_data" ></Table>
    
    <div class="Company-details-title">
      <h4>其他材料</h4>
    </div>
    <Table border :columns="other_materials" :data="other_data" ></Table>
    <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
          <el-form ref="form" :model="form" label-width="50px">
              <el-form-item label="">
                  <p class="text-left">{{form.one}}</p>
              </el-form-item>
              <el-form-item label="">
                  <el-input v-model="form.two" :readonly="form.two == '查看'"></el-input>
              </el-form-item>
              <el-form-item label="">
                  <p class="text-left">{{form.three}}</p>
              </el-form-item>
              <el-form-item label="">
                  <el-input v-model="form.four" :readonly="form.four == '查看'"></el-input>
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="editVisible = false">取 消</el-button>
              <el-button type="primary" @click="saveEdit">确 定</el-button>
          </span>
      </el-dialog>
  </div>
</template>

