          two:'查看',
          three:'',
          four:'',
        }
      ]
    }
  },
  methods:{
    succ () {
      this.$router.push('/Cooperative_enterprise');
      
    },
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        // 将数据放在当前组件的数据内
        if(routerParams == 'Renewal'){
          this.change_btn = routerParams
        }
        if(routerParams == 'Pause'){
          this.change_btn = routerParams
        }
        if(this.change_btn == 1){
          this.change_btn = 'Renewal'
        }
    },
    cancel () {
      this.$router.push('/Cooperative_enterprise');
    },
    // 保存编辑
    saveEdit() {
        this.$set(this.companyInfo, this.idx, this.form);
        this.editVisible = false;
        //this.$message.success(`修改第 ${this.idx+1} 行成功`);
        this.$Message.success(`修改第 ${this.idx+1} 行成功`);
    },
  },
  mounted() {
    this.getParams ()
  }
