</script>

<style lang="css">
.Company-details{
  
}
.Company-details .search_criteria{
  height: 50px;
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
.Company-details .contract-row{
  line-height: 30px;
  margin-top: 20px;
}
.Company-details .ivu-select-dropdown{
  z-index: 99999
}
.Company-details .Company-details-title{
  text-align: left;
  margin: 20px 0;
  margin-left: 30px;
}
</style>



